/* Reset some default styling */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

/* Body background color */
body {
    background-color: #f2f7f5;
    font-family: Arial, sans-serif;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

/* Container styles */
.container {
    max-width: 600px;
    margin: 0 auto;
    padding: 20px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 0px 10px rgba(0, 128, 0, 0.2); /* Tono verde en el sombreado */
}

/* Form heading styles */
h1 {
    font-size: 28px;
    color: #007f00; /* Verde oscuro */
    margin-bottom: 20px;
    text-align: center;
}

/* Form styles */
.form-group {
    margin-bottom: 20px;
}

/* Label styles */
label {
    font-weight: bold;
    display: block;
    margin-bottom: 10px;
    color: #007f00; /* Verde oscuro */
}

/* Input styles */
input, select {
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 12px;
    width: 100%;
    font-size: 16px;
}

/* Submit button styles */
.submit-button {
    background-color: #007f00; /* Verde oscuro */
    border: none;
    color: #fff;
    padding: 12px 20px;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    transition: background-color 0.3s ease;
    width: 100%;
}

.submit-button:hover {
    background-color: #005a00; /* Verde más oscuro al pasar el ratón */
}

/* Center elements */
.text-center {
    text-align: center;
}

/* Responsive adjustments */
@media (max-width: 768px) {
    .container {
        max-width: 100%;
        padding: 10px;
    }

    h1 {
        font-size: 24px;
    }

    input, select {
        font-size: 16px;
    }

    .submit-button {
        font-size: 16px;
    }
}
